import { IonContent, IonHeader, IonPage, IonTitle, IonToolbar } from '@ionic/react';
import './Tab2.css';
import {useEffect, useState} from "react";
import {
    useIonViewDidEnter,
    useIonViewDidLeave,
    useIonViewWillEnter,
    useIonViewWillLeave
} from '@ionic/react';
import QrReader from 'modern-react-qr-reader'

const Tab2: React.FC = () => {

    const [data,setData] = useState<string | null>(null);
    const [showCam,setShowCam] = useState(true);
    const [scanTimedOut,setScanTimedOut] = useState(false);

    useIonViewDidEnter(() => {
        console.log('ionViewDidEnter event fired');
        setShowCam(true);
    });

    useIonViewDidLeave(() => {
        console.log('ionViewDidLeave event fired');
        setShowCam(false);
    });

    useIonViewWillEnter(() => {
        console.log('ionViewWillEnter event fired');
    });

    useIonViewWillLeave(() => {
        console.log('ionViewWillLeave event fired');
    });


    useEffect(
        () => {
            if(data === null && !scanTimedOut) {
                let timer1 = setTimeout(() => setScanTimedOut(true), 60*1000); // 1 minute

                // this will clear Timeout
                // when component unmount like in willComponentUnmount
                // and show will not change to true
                return () => {
                    clearTimeout(timer1);
                };
            }
        },
        // useEffect will run only one time with empty []
        // if you pass a value to array,
        // like this - [data]
        // than clearTimeout will run every time
        // this value changes (useEffect re-run)
        [data,scanTimedOut]
    );

    const handleScan = data => {
        if (data) {
            console.log(data);
            setData(data);
        }
    }

    const handleError = err => {
        console.error(err)
    }

    const isValidUrl = (url) => {
        try {
            new URL(url);
        } catch (e) {
            console.error(e);
            return false;
        }
        return true;
    };


    function DataSection(props){
        const { data } = props;
        const [urlInfo,setUrlInfo] = useState<any | null>(null);


        useEffect(() => {
            setUrlInfo({validUrl: isValidUrl(data)})
        },data);

        if(!urlInfo){
            return <div>Loading url info</div>;
        }else{
            return <div>{urlInfo.validUrl ? <a href={data} target="_blank">{data}</a> : <textarea rows={6} cols={40} readOnly onClick={(e) => { (e.target as HTMLTextAreaElement).select() } }>{data}</textarea>}</div>;
        }

    }

    // useEffect(() => {
    //     function onScanSuccess(qrCodeMessage: string) {
    //         html5QrcodeScanner.clear();
    //         setData(qrCodeMessage);
    //     }
    //
    //     function onScanFailure(error: any) {
    //         // handle scan failure, usually better to ignore and keep scanning.
    //         // for example:
    //         console.warn(`QR error = ${error}`);
    //     }
    //
    //
    //     var html5QrcodeScanner = new Html5QrcodeScanner(
    //         "qr-reader", { fps: 10, qrbox: 250, facingMode: "environment"  });
    //     html5QrcodeScanner.render(onScanSuccess,onScanFailure);
    //     // Specify how to clean up after this effect:
    //     return function cleanup() {      html5QrcodeScanner.clear();    };
    // }, [data]);

    return (
    <IonPage>
      <IonHeader>
        <IonToolbar>
          <IonTitle>Scan QR Code</IonTitle>
        </IonToolbar>
      </IonHeader>
      <IonContent fullscreen>
        <IonHeader collapse="condense">
          <IonToolbar>
            <IonTitle size="large">Scan QR Code</IonTitle>
          </IonToolbar>
        </IonHeader>
          {scanTimedOut && <span>timed out <p><button onClick={()=>setScanTimedOut(false)}>Scan Again</button></p></span>}
          {!scanTimedOut && showCam && !data && <QrReader
              delay={500}
              facingMode={"environment"}
              onError={handleError}
              onScan={handleScan}
              style={{ width: '100%', maxWidth: 600, padding: 10 }}
          />}
          {data ? <div id="qr-reader-results"><h1>data:</h1><DataSection data={data}></DataSection>
              <p><button onClick={()=>setData(null)}>Scan Again</button></p>
          </div> : <div style={{backgroundColor: 'yellow'}}>Nothing yet</div>}
          {/* JSON.stringify({data,showCam,scanTimedOut}) */}
      </IonContent>
    </IonPage>
  );
};

export default Tab2;
