import { IonContent, IonHeader, IonPage, IonTitle, IonToolbar } from '@ionic/react';
import { useState,useEffect } from 'react';
import './Tab1.css';
const QRCode = require('qrcode');

function download(dataurl: string, filename: string) {
  var a = document.createElement("a");
  a.href = dataurl;
  a.setAttribute("download", filename);
  a.click();
}


const Tab1: React.FC = () => {
  const [svg,setSvg] = useState<string | null>(null);
  const [text,setText] = useState('');

  const HandleChangeText = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    const text=e.target.value;

    setText(text);
  }


  const downloadPng = () =>{
    if(text !==''){
      QRCode.toDataURL(text, {type: 'image/png'}, function (err: Error, url: string) {
        if (err) throw err;
        download(url,'qr.png')
      })
    }
  }
  

  useEffect(() => {
    if(text!==''){
      QRCode.toString(text,{type:'svg'},function (err: Error, svgdata: string) {
        if (err) throw err;
        setSvg(svgdata);
      });
    }else{
      setSvg(null);
    }
  }, [text]); 


  return (
    <IonPage>
      <IonHeader>
        <IonToolbar>
          <IonTitle>Generate QR Code</IonTitle>
        </IonToolbar>
      </IonHeader>
      <IonContent fullscreen>
        <IonHeader collapse="condense">
          <IonToolbar>
            <IonTitle size="large">Generate QR Code</IonTitle>
          </IonToolbar>
        </IonHeader>
        <p>
<label htmlFor="qr_text">Enter Text: </label>
<br/>
<textarea rows={3} id="qr_text" onChange={HandleChangeText} style={{width: '90%',maxWidth: 600}} value={text}/>
<button onClick={()=> setText('')}>Clear</button>
<br/>
<div id="container" style={{maxWidth: 320}}>{typeof svg === 'string' ? 
<div><span  dangerouslySetInnerHTML={{ __html: svg!}}></span>
<button onClick={downloadPng}>Download PNG</button>
</div> :<span>No text entered</span>}</div>
</p>

      </IonContent>
    </IonPage>
  );
};

export default Tab1;
